import React, { useRef, useState } from 'react';
import './GradientButton.css';
import { IoDocument } from 'react-icons/io5';

function GradientButton() {
  const buttonRef = useRef(null);
  const [gradientPosition, setGradientPosition] = useState(null);

  const handleMouseMove = (e) => {
    const rect = buttonRef.current.getBoundingClientRect();
    const x = ((e.clientX - rect.left) / rect.width) * 100; // percentage position within the button
    const y = ((e.clientY - rect.top) / rect.height) * 100; // percentage position within the button

    setGradientPosition({ x, y });
  };

  const handleMouseLeave = () => {
    setGradientPosition(null);
  };

  return (
    <button
      ref={buttonRef}
      onClick={() => window.open('./Resume.pdf')}
      className="gradientButton"
      onMouseMove={handleMouseMove}
      target="_blank"
      onMouseLeave={handleMouseLeave}
      style={{
        background: gradientPosition
          ? `radial-gradient(circle at ${gradientPosition.x}% ${gradientPosition.y}%, red, blue)`
          : 'blue',
        fontSize: '1.3rem',
      }}
    >
      Resume
      <IoDocument
        style={{
          marginLeft: '20px',
          position: 'relative',
          top: '3px',
        }}
      />
    </button>
  );
}

export default GradientButton;
